.hero{
    width: 100%;
    height: 70vh;
    background-color: rgb(238, 235, 235);
    display: flex;
    justify-content: center;
    
    
}
.content-hero{
    margin-top: 200px;
}
.hero-title{
    text-align: center;
    font-weight: 900;
}
.hero-sub-title{
    text-align: center;
    font-weight: 800;
    font-size: 2.4rem;
    color: #04A9F4;
}
.hero-description{
    text-align: center;
    color: gray;

}
.hero-btn-div{
    display: flex;
    justify-content: center;
    margin-top: 20px;
}
.hero-btn{
   padding: 15px;
   color: white;
   background-color: #04A9F4;
   opacity: 0.7;
   border: none;
   border-radius: 5px;
   cursor: pointer;
   transition: 0.3s;
   letter-spacing: 1.3px;

}
.hero-btn:hover{
    opacity: 1;
    transition: 0.3s;
}