.footer{
    background-color: #ccebf9;
   
}
.footer-container{
    width: 100%;
    padding: 70px 30px 20px;
}
.social-icons{
    display: flex;
    justify-content: center;
}
.social-icons a{
    padding: 10px;
    text-decoration: none;
    font-size: 30px;
    margin: 10px;
    border-radius: 50%;
    opacity: 0.9;
    color: black;
  
}
.social-icons a i{
    font-size: 2em;
    opacity: 0.9;
}
.footer-nav{
    margin: 30px 0;
}
.footer-nav ul{
    display: flex;
    justify-content: center;
}

.footer-nav ul li a{
    color: black;
    margin: 20px;
    font-size: 1.3em;
    opacity: 0.7;
    transform: 0.5s;
}
.footer-nav ul li a:hover{
    opacity: 1;
}
.footer-bottom{
     padding: 20px;
     text-align: center;
}
.footer .logo{
    text-align: center;
    padding-top: 2rem;
}
.designer{
    opacity: 0.7;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 400;
    margin :0px 5px
}
/* responsive */
@media (max-width:700px){
    .footer-nav ul  .li{
        display: flex !important ;
        width: 100% !important;
        display: flex;
        justify-content: center;
        margin: 10px ;
    }
    .footer-nav ul{
        flex-direction: column;
    }
   
}