
.main{
    display: flex;
    justify-content: space-around;
    align-items: center;
}


.features-content{
    color: white;
    display: flex;
    flex-wrap: wrap;
   justify-content: center;
    width: 100%;
    flex-direction: row-reverse;
    
}
.features-content .row h2{
    margin-top: 2rem;
    text-align: center;
    margin-bottom: 6px;
    color: black;
}
.features-content .row p{
    text-align: center;
    margin-bottom: 6px;
    line-height: 25px;
    font-size: 15px;
   
}
.features-content .row{
    color: gray;
    display: flex;
    justify-content: center;
    align-items: center;
   width: 90%;
    width: 50%;
  
  
}
