.main-nav{
    display: flex;
    justify-content: center;
    background-color: rgb(238, 235, 235);
}
nav{
    height: 90px;
    border-radius: 10px;
    margin-top: 15px;
    transition: 0.5s;
    width: 80%;
    background-color:#c5e5f4;
    position: fixed;
    z-index: 1;
    
  
}
 .colorChange{
    width: 100%;
    transition: 0.5s;
    margin-top: 0px;
 }

.logo{
    font-size: 30px;
    color: #04A9F4 !important;
}
label.logo{
    color: black;
    padding: 0 100px;
    line-height: 80px;
}
.logoName{
    font-family: 'Courier New', Courier, monospace;
    margin-left: 7px;
    font-size: 20px;
    color: black;
}
nav ul {
    float: right;
    margin-right: 20px;
}
nav ul li {
    display: inline-block;
    line-height: 80px;
    margin: 0 5px;
}
nav ul li a {
    color: black;
    font-size: 17px;
    padding: 7px 13px;
    text-transform: uppercase;
    transition: .5s;
}
 nav ul li a:hover{
    color: #04A9F4;
    border-radius: 5px;
    transition: .5s ;
}
.checkbtn{
    color:#04A9F4;
    float: right;
    line-height: 80px;
    margin-right: 40px;
    display: none;
    margin-top: 0.2rem;
    font-size: 25px;
    cursor: pointer ;
}
#check{
    display: none;
}
@media (max-width :952px){
label.logo{
    font-size: 30px;
    padding-left: 50px;
}
nav ul li a{
    font-size: 16px;
}
}
@media (max-width :952px){
  .logoName{
    font-size: 15px;
  }
  ul i{
    display: none !important;
  }
  nav{
    background-color: rgb(238, 235, 235);
    width: 100%;
  }
    }
@media (max-width:858px){
    .checkbtn{
        display: block;
    }
   nav   ul{
        position: fixed;
        width: 60%;
        height: 50vh;
        border-radius: 20px;
        background-color:white;
        top: 120px;
        left: -100%;
        transition: all .5s;
        
        text-align: left;
    }
    nav ul li {
        display: block;
        margin-left: 30px;
    }
    nav ul li a{
        font-size: 20px;
        color: black;
        opacity: 0.8;
        
    }
    #check:checked ~ul {
        left: 0;
    }

}
